import Vue from 'vue'
import Router from 'vue-router'
import Share from '@/components/Share'
import Picture from '@/components/Picture'
import Video from '@/components/Video'
import Release from '@/components/Release'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: { name: 'Share' }
    },
    {
      path: '/Share',
      name: 'Share',
      component: Share
    },
    {
      path: '/Picture',
      name: 'Picture',
      component: Picture
    },
    {
      path: '/Video',
      name: 'Video',
      component: Video
    },
    {
      path: '/Release',
      name: 'Release',
      component: Release
    }
  ]
})



