<template>
  <div>
    <div class="shareDiv" v-show="!nextPage">
      <div class="start">
        <div class="startP">拍照<input @change="handleInput('image')" type="file" accept="image/*" capture="camera"></div>
        <div class="startV" @click="jump('Video')">录像<!-- <input @change="handleInput('video')" type="file" > --></div>
      </div>
    </div>
    <div class="ReleaseDiv" v-show="nextPage">
      <div class="source">
        <img v-show="openImage" :src="image">
        <video v-show="openVideo" :src="video" autoplay loop>
        </video>
      </div>
      <div class="start">
        <div class="back" @click="back">返回</div>
        <div class="startV" @click="upLoad">发布</div>
      </div>
      <div class="model" v-show="tip">
        <div class="success" @click="back">发布成功</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'home',
  // props: ['file'],
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      show: false,
      file: {},
      openImage: false,
      openVideo: false,
      image: '',
      video: '',
      nextPage: false,
      tip: false
    }
  },
  methods: {
    handleInput (val) {
      alert(1)
      this.nextPage = true
      console.log(event)
      this.file = event.target.files[0]
      let reader = new FileReader()
      reader.readAsDataURL(this.file)
      reader.onload = function (e) {
        if (val === 'image') {
          this.openImage = true
          this.image = e.target.result
        } else if (val === 'video') {
          // console.log(e.target.result)
          this.openVideo = true
          this.video = e.target.result
        }
        // this.image = e.target.result
        // console.log(this.image)
        // this.$emit('changeFile', e.target.result)
      }.bind(this)
      // this.$emit('changeFile', event.target.files[0])
      // this.$router.push({path: '/' + to})
    },
    back () {
      this.nextPage = false
      this.tip = false
      this.file = {}
      this.openImage = false
      this.openVideo = false
      this.image = ''
      this.video = ''
      // this.$router.go(-1)
    },
    upLoad () {
      this.tip = true
    },
    jump (to) {
      // this.nextPage = false
      // this.tip = false
      // this.file = {}
      // this.openImage = false
      // this.openVideo = false
      // this.image = ''
      // this.video = ''
      this.$router.push({path: '/' + to})
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.shareDiv {
  width: 100vw;
  max-width: 100%;
  height: 100vh;
}
.shareDiv >.start {
  width: 100%;
  height: 100%;
  margin: 0 auto;
  position: relative;
  background: url('../../static/img/bg-1.png') center / auto 100% no-repeat;
}
.shareDiv >.start >div {
  /* width: 3.98rem; */
  /* height: 1.75rem; */
  width: calc(398 / 1206 * 100vh);
  height: calc(175 / 1206 * 100vh);
  font-size: 0;
  position: absolute;
  left: 50%;
  margin-left: calc(-398 / 1206 * 100vh / 2);
  cursor: pointer;
}
.shareDiv >.start >div >input {
  width: 100%;
  height: 100%;
  font-size: 0;
}
.shareDiv >.start >.startP {
  bottom: calc(240 / 1206 * 100vh);
  background: url('../../static/img/picture.png') center / auto 100% no-repeat;
}
.shareDiv >.start >.startV {
  bottom: calc(90 / 1206 * 100vh);
  background: url('../../static/img/video.png') center / auto 100% no-repeat;
}
@media screen and (max-device-aspect-ratio: 750 / 1206) {
  .shareDiv {
    height: calc(100vw * 1206 / 750);
    /* margin-top: calc(50vh - (100vw * 1206 / 750) / 2); */
  }
  .shareDiv >.start >div {
    width: 3.98rem;
    height: 1.75rem;
    margin-left: calc(-3.98rem / 2);
  }
  .shareDiv >.start >.startP {
    bottom: 2.4rem;
  }
  .shareDiv >.start >.startV {
    bottom: .9rem;
  }
}
.ReleaseDiv {
  width: 100vw;
  max-width: 100%;
  height: 100vh;
  position: relative;
}
.ReleaseDiv >.model {
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, .7);
  position: absolute;
  top: 0;
  left: 0;
  z-index: 100;
}
.ReleaseDiv >.model >.success {
  width: calc(334 / 1206 * 100vh);
  height: calc(119 / 1206 * 100vh);
  margin: calc(50vh - 119 / 1206 * 100vh / 2) auto 0;
  font-size: 0;
  background: url('../../static/img/success.png') center / auto 100% no-repeat;
}
.ReleaseDiv >.source {
  width: calc(485 / 1206 * 100vh);
  height: calc(491 / 1206 * 100vh);
  /* background: #f0f; */
  position: absolute;
  top: calc(224 / 1206 * 100vh);
  left: 50%;
  z-index: 10;
  margin-left: calc(-234 / 1206 * 100vh);
  /* background: url('../../static/img/Release-btn.png') center / auto 100% no-repeat; */
}
.ReleaseDiv >.source >img {
  width: 100%;
  height: 100%;
}
.ReleaseDiv >.source >video {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.ReleaseDiv >.start {
  width: 100%;
  height: 100%;
  margin: 0 auto;
  position: relative;
  z-index: 20;
  background: url('../../static/img/Release-bg.png') center / auto 100% no-repeat;
}
.ReleaseDiv >.start >div {
  /* width: 3.98rem; */
  /* height: 1.75rem; */
  width: calc(398 / 1206 * 100vh);
  height: calc(175 / 1206 * 100vh);
  font-size: 0;
  position: absolute;
  left: 50%;
  margin-left: calc(-398 / 1206 * 100vh / 2);
  cursor: pointer;
}
.ReleaseDiv >.start >.back {
  width: calc(153 / 1206 * 100vh);
  height: calc(65 / 1206 * 100vh);
  font-size: 0;
  position: absolute;
  z-index: 30;
  top: calc(42 / 1206 * 100vh);
  left: 50%;
  margin-left: calc(-700 / 1206 * 100vh / 2);
}
.ReleaseDiv >.start >.startV {
  bottom: calc(90 / 1206 * 100vh);
  background: url('../../static/img/Release-btn.png') center / auto 100% no-repeat;
}
@media screen and (max-device-aspect-ratio: 750 / 1206) {
  .ReleaseDiv {
    height: calc(100vw * 1206 / 750);
    /* margin-top: calc(50vh - (100vw * 1206 / 750) / 2); */
  }
  .ReleaseDiv >.model >.success {
    width: calc(334 / 750 * 100vw);
    height: calc(119 / 750 * 100vw);
    margin: calc(750 / 1206 * 100vh / 2) auto 0;
  }
  .ReleaseDiv >.source {
    width: calc(485 / 750 * 100vw);
    height: calc(491 / 750 * 100vw);
    position: absolute;
    top: calc(224 / 750 * 100vw);
    left: calc(141 / 750 * 100vw);
    z-index: 10;
    margin-left: 0;
  }
  .ReleaseDiv >.start >div {
    width: 3.98rem;
    height: 1.75rem;
    margin-left: calc(-3.98rem / 2);
  }
  .ReleaseDiv >.start >.back {
    width: calc(153 / 750 * 100vw);
    height: calc(65 / 750 * 100vw);
    top: calc(42 / 750 * 100vw);
    left: calc(28 / 750 * 100vw);
    margin-left: 0;
  }
  .ReleaseDiv >.start >.startV {
    bottom: .9rem;
  }
}
</style>



