<template>
  <div class="pictureDiv">
    <div class="top">
      <video ref="video" autoplay v-show="!final"></video>
      <canvas ref="canvas" v-show="final"></canvas>
    </div>
    <div class="bottom" v-show="!final">
      <div class="photo"><img v-if="picture.done" src="static/img/btn.png" width="100%"><input type="file" accept="image/*" ></div>
      <div class="btn" @click="take"><span>点击拍照</span><div></div></div>
      <div class="metathesis" @click="change"></div>
    </div>
    <div class="after" v-show="final">
      <div class="again" @click="again"></div>
      <div class="ok" @click="jump('Release')"></div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      picture: {
        done: false
      },
      direction: 0, // 如果存在前摄像头，优先使用前摄像头
      exArray: [],
      timer: '',
      final: false
    }
  },
  mounted: function () {
    // console.log(window)
    this.openCamera()
  },
  methods: {
    openCamera () {
      navigator.mediaDevices.enumerateDevices().then(function (MediaDeviceInfo) {
        for (let k = 0, length = MediaDeviceInfo.length; k < length; k++) {
          if (MediaDeviceInfo[k].kind === 'videoinput' || MediaDeviceInfo[k].kind === 'video') {
            this.exArray.push(MediaDeviceInfo[k].deviceId)
          }
        }
      }.bind(this))
      let video = this.$refs.video
      // video.width = window.innerWidth
      // video.height = window.innerHeight
      let videoObj = {
        audio: false,
        video: {
          'optional': [
            {
              'sourceId': this.exArray[this.direction]
            }
          ]
        }
      }
      navigator.mediaDevices.getUserMedia(videoObj).then(function (mediaStream) {
        video.srcObject = mediaStream
        video.play()
      }).catch(function (error) {
        console.log(error)
      })
      // let canvas = this.$refs.canvas
      // canvas.width = window.innerWidth
      // canvas.height = window.innerHeight
      // let context = canvas.getContext('2d')
      // this.timer = setInterval(function () {
      //   context.drawImage(video, 0, 0, window.innerWidth, window.innerHeight)
      // }, 20)
    },
    change () {
      if (this.direction === 0) {
        this.direction = 1
      } else {
        this.direction = 0
      }
      this.openCamera()
    },
    take () {
      this.final = true
      let video = this.$refs.video
      let canvas = this.$refs.canvas
      let context = canvas.getContext('2d')
      canvas.width = window.innerWidth
      canvas.height = window.innerHeight
      // let width = video.clientWidth // 640
      // let height = video.clientHeight // 480
      // let x = height * 640 / 480
      // let y = height
      // canvas.width = x
      // canvas.height = y
      // let _x = 480 * x / y
      // let _y = 480
      // context.drawImage(video, (640 - _x) / 2, 0, (640 - _x) / 2 * y / x, _y, 0, 0, x, y)
      context.drawImage(video, (640 - window.innerWidth * 480 / window.innerHeight) / 2, 0, window.innerWidth * 480 / window.innerHeight, 480, 0, 0, window.innerWidth, window.innerHeight)
      // console.log(video)
      // console.log((640 - _x) / 2 / (640 + _x) * 2)
      // console.log(375/812)
      // clearInterval(this.timer)
    },
    again () {
      this.final = false
      // let video = this.$refs.video
      // let canvas = this.$refs.canvas
      // let context = canvas.getContext('2d')
      // this.timer = setInterval(function () {
      //   context.drawImage(video, 0, 0, window.innerWidth, window.innerHeight)
      // }, 20)
    },
    jump (to) {
      this.$router.push({path: '/' + to})
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.pictureDiv {
  width: 100vw;
  max-width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  background: #000;
}
.pictureDiv >.top{
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.pictureDiv >.top >video{
  /* width: calc(640 / 480 * 100vh); */
  width: 100vw;
  height: 100vh;
  object-fit: cover;
  flex-shrink: 0;
}
.pictureDiv >.top >canvas{
/*   width: 100vw;
  height: 100vh; */
  display: flex;
  flex-shrink: 0;
}
.pictureDiv >.bottom{
  width: 100%;
  height: calc(254 / 750 * 100vw);
  position: fixed;
  left: 0;
  bottom: 0;
  z-index: 100;
  background: #000;
  border-top: 1px solid #166c91;
  flex-shrink: 0;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}
.bottom >div {
  flex-shrink: 0;
  cursor: pointer;
}
.bottom >.photo {
  width: calc(104 / 750 * 100vw);
  height: calc(104 / 750 * 100vw);
  background: url('../../static/img/photo-bg.png') center / auto 100% no-repeat;
  margin-left: calc(44 / 750 * 100vw);
  margin-bottom: calc(54 / 750 * 100vw);
  position: relative;
}
.bottom >.photo >input {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 100;
  font-size: 0;
}
.bottom >.photo >img {
  width: calc(74 / 750 * 100vw);
  height: calc(74 / 750 * 100vw);
  /* background: #f00; */
  border-radius: calc(37 / 750 * 100vw);
  /* margin-top: calc(10 / 750 * 100vw); */
  /* margin-bottom: calc(54 / 750 * 100vw); */
}
.bottom >.btn {
  width: calc(296 / 750 * 100vw);
  height: calc(251 / 750 * 100vw);
  cursor: auto;
  background: url('../../static/img/btn-bg.png') center / auto 100% no-repeat;
}
.bottom >.btn >span {
  display: flex;
  justify-content: center;
  font-size: .24rem;
  letter-spacing: 0.02rem;
  margin-top: calc(20 / 750 * 100vw);
  color: #fff;
}
.bottom >.btn >div {
  width: calc(145 / 750 * 100vw);
  height: calc(145 / 750 * 100vw);
  display: block;
  cursor: pointer;
  margin: calc(30 / 750 * 100vw) auto 0;
  background: url('../../static/img/btn.png') center / auto 100% no-repeat;
}
.bottom >.metathesis {
  width: calc(104 / 750 * 100vw);
  height: calc(107 / 750 * 100vw);
  background: url('../../static/img/metathesis-bg.png') center / auto 100% no-repeat;
  margin-right: calc(44 / 750 * 100vw);
  margin-bottom: calc(50 / 750 * 100vw);
}
.pictureDiv >.after {
  width: 100%;
  height: calc(500 / 750 * 100vw);
  position: fixed;
  left: 0;
  bottom: 0;
  z-index: 200;
  flex-shrink: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.after >div {
  width: calc(398 / 750 * 100vw);
  height: calc(175 / 750 * 100vw);
  cursor: pointer;
}
.after >.again {
  background: url('../../static/img/again.png') center / auto 100% no-repeat;
}
.after >.ok {
  background: url('../../static/img/ok.png') center / auto 100% no-repeat;
}
</style>



