<template>
  <div id="app">
    <!-- <router-view @changeFile="changeFile" :file="file" /> -->
    <router-view />
  </div>
</template>

<script>
export default {
  name: 'App',
  data () {
    return {
      // file: []
    }
  },
  mounted: function () {
    window.addEventListener('contextmenu', function (e) {
      console.log('禁止右击菜单')
      e.preventDefault()
    })
  },
  methods: {
    // changeFile (msg) {
    //   this.file = msg
    // }
  }
}
</script>

<style>
* {
  border: none;
  margin: 0;
  padding: 0;
  -webkit-touch-callout: none;/*系统默认菜单被禁用*/
  -webkit-user-select: none;/*禁用文本复制*/
  -webkit-tap-highlight-color: transparent;/*取消点击链接或者JavaScript可点击元素赋给的高亮背景色*/
  outline: none;
}
html, body {
  font-size: calc(100vw / 750 * 100);
  background: #00080b;
  width: 100vw;
  max-width: 100%;
  height: 100vh;
  max-height: 100vh;
  overflow: hidden;
}
input[type="file" i]::-webkit-file-upload-button {
  border: none;
  padding: 0;
}
#app {
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
</style>



