<template>
  <div>
    <div class="pictureDiv" v-show="!nextPage">
      <div class="top">
        <video ref="video1" autoplay v-show="!recordVideo"></video>
        <video ref="video2" autoplay v-show="recordVideo"></video>
  <!--       <video ref="video" autoplay v-show="!final"></video>
        <canvas ref="canvas" v-show="final"></canvas> -->
      </div>
      <div class="bottom" v-show="!final">
        <div class="btn"><span>点击录像[最长10秒]</span><div @touchstart="take" @touchend="end"></div></div>
      </div>
      <div class="after" v-show="final">
        <div class="again" @click="again"></div>
        <div class="ok" @click="jump('Release')"></div>
      </div>
    </div>
    <div class="ReleaseDiv" v-show="nextPage">
      <div class="source">
        <video ref="video3" v-show="openVideo" :src="video" :autoplay="nextPage" loop>
        </video>
      </div>
      <div class="start">
        <div class="back" @click="back">返回</div>
        <div class="startV" @click="upLoad">发布</div>
      </div>
      <div class="model" v-show="tip">
        <div class="success" @click="back">发布成功</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      picture: {
        done: false
      },
      direction: 0, // 如果存在前摄像头，优先使用前摄像头
      exArray: [],
      mediaSource: {},
      recordVideo: false,
      recordedChunks: [],
      sourceBuffer: null,
      theStream: null,
      theRecorder: null,
      final: false,
      openVideo: false,
      video: '',
      nextPage: false,
      tip: false
    }
  },
  mounted: function () {
    // console.log(this)
    // this.$http.get('/api/data').then(res => {
    //   console.log(res)
    // })
    this.openCamera()
    this.mediaSource = new MediaSource()
  },
  methods: {
    openCamera () {
      navigator.mediaDevices.enumerateDevices().then(function (MediaDeviceInfo) {
        for (let k = 0, length = MediaDeviceInfo.length; k < length; k++) {
          if (MediaDeviceInfo[k].kind === 'videoinput' || MediaDeviceInfo[k].kind === 'video') {
            this.exArray.push(MediaDeviceInfo[k].deviceId)
          }
        }
      }.bind(this))
      let video1 = this.$refs.video1
      // video.width = window.innerWidth
      // video.height = window.innerHeight
      let videoObj = {
        audio: true,
        video: {
          'optional': [
            {
              'sourceId': this.exArray[this.direction]
            }
          ]
        }
      }
      navigator.mediaDevices.getUserMedia(videoObj).then(function (mediaStream) {
        this.theStream = mediaStream
        video1.srcObject = mediaStream
        video1.play()
      }.bind(this)).catch(function (error) {
        console.log(error)
      })
    },
    take (event) {
      // let video = this.$refs.video
      this.recordVideo = true
      let video2 = this.$refs.video2
      video2.src = window.URL.createObjectURL(this.mediaSource)
      console.log(window)
      this.mediaSource.addEventListener('sourceopen', function (e) {
        this.sourceBuffer = this.mediaSource.addSourceBuffer(`video/webm; codecs="opus,vp8"`)
      }.bind(this), false)
      let recorder
      try {
        recorder = new MediaRecorder(this.theStream)
      } catch (e) {
        console.error('Exception while creating MediaRecorder: ' + e)
        return
      }
      this.theRecorder = recorder
      console.log('MediaRecorder created')
      recorder.ondataavailable = this.recorderOnDataAvailable
      recorder.start(100)
      // setTimeout(function () {
      //   this.end()
      // }.bind(this), 3100)
    },
    recorderOnDataAvailable (event) {
      if (event.data.size === 0) return
      this.recordedChunks.push(event.data)

      let reader = new FileReader()
      reader.addEventListener('loadend', function () {
        let arr = new Uint8Array(reader.result)
        this.sourceBuffer.appendBuffer(arr)
      }.bind(this))
      reader.readAsArrayBuffer(event.data)
    },
    end () {
      try {
        this.final = true
        this.theRecorder.stop()
        this.theStream.getTracks()[0].stop()
        let blob = new Blob(this.recordedChunks, {type: 'video/webm'})
        let url = (window.URL || window.webkitURL).createObjectURL(blob)
        // this.video = 'close' + url
        this.video = url
        // video3.play()
      } catch (e) {
        console.log(e)
      }
    },
    again () {
      this.recordVideo = false
      this.final = false
    },
    back () {
      this.nextPage = false
      this.tip = false
      this.openVideo = false
      this.video = ''
      // this.$router.go(-1)
    },
    upLoad () {
      // this.tip = true
      // var a = document.createElement('a')
      // document.body.appendChild(a)
      // a.style = 'display: none'
      // a.href = this.video
      // a.download = new Date().getTime() + '.webm'
      // a.click()
      let videoFile = new FormData()
      let blob = new Blob(this.recordedChunks, {type: 'video/webm'})
      // let url = (window.URL || window.webkitURL).createObjectURL(blob)
      console.log(this.recordedChunks)
      console.log(blob)
      videoFile.append('videofile', blob, new Date().getTime() + '.webm')
      // videoFile.append('videofile', blob)
      console.log(videoFile.getAll('videofile'))
      // let options = {
      //   headers: {
      //     'Content-Type': 'multipart/form-data'
      //   },
      //   processData: false,
      //   contentType: false,
      //   responseType: 'blob'
      // }
      let options = {
        headers: {
          'Content-Type': 'application/octet-stream'
        },
        responseType: 'blob'
      }
      console.log(videoFile)
      this.$http.post('/api/upload', videoFile, options).then(res => {
        console.log(res)
      })
      // this.$http.post('upload.php', videoFile, options).then(res => {
      //   console.log(res)
      // })
    },
    dataURItoBlob (dataURI) {
      // let byteString = atob((dataURI.split(',')[1]))
      let byteString = dataURI
      let mimeString = dataURI.split(',')[0].split(':')[1].split(';')[0]
      let ab = new ArrayBuffer(byteString.length)
      let ia = new Uint8Array(ab)
      for (let i = 0; i < byteString.length; i++) {
        ia[i] = byteString.charCodeAt(i)
      }
      return new Blob([ab], {type: mimeString})
    },
    jump (to) {
      let video3 = this.$refs.video3
      video3.play()
      this.nextPage = true
      this.openVideo = true
      // this.video = this.video.slice(5)
      // this.$router.push({path: '/' + to})
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.pictureDiv {
  width: 100vw;
  max-width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  background: #000;
}
.pictureDiv >.top{
  width: 100%;
  height: 100%;
}
.pictureDiv >.top >video{
  width: 100vw;
  height: 100vh;
  object-fit: cover;
}
.pictureDiv >.top >canvas{
  width: 100vw;
  height: 100vh;
}
.pictureDiv >.bottom{
  width: 100%;
  height: calc(254 / 750 * 100vw);
  position: fixed;
  left: 0;
  bottom: 0;
  z-index: 100;
  border-top: 1px solid #166c91;
  flex-shrink: 0;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  background: #000;
}
.bottom >div {
  flex-shrink: 0;
  cursor: pointer;
}
.bottom >.btn {
  width: calc(296 / 750 * 100vw);
  height: calc(251 / 750 * 100vw);
  cursor: auto;
  background: url('../../static/img/btn-bg.png') center / auto 100% no-repeat;
}
.bottom >.btn >span {
  display: flex;
  justify-content: center;
  font-size: .24rem;
  letter-spacing: 0.02rem;
  margin-top: calc(20 / 750 * 100vw);
  color: #fff;
}
.bottom >.btn >div {
  width: calc(145 / 750 * 100vw);
  height: calc(145 / 750 * 100vw);
  display: block;
  cursor: pointer;
  margin: calc(30 / 750 * 100vw) auto 0;
  background: url('../../static/img/video-start.png') center / auto 100% no-repeat;
}
.pictureDiv >.after {
  width: 100%;
  height: calc(500 / 750 * 100vw);
  position: fixed;
  left: 0;
  bottom: 0;
  z-index: 200;
  flex-shrink: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.after >div {
  width: calc(398 / 750 * 100vw);
  height: calc(175 / 750 * 100vw);
  cursor: pointer;
}
.after >.again {
  background: url('../../static/img/again.png') center / auto 100% no-repeat;
}
.after >.ok {
  background: url('../../static/img/ok.png') center / auto 100% no-repeat;
}
.ReleaseDiv {
  width: 100vw;
  max-width: 100%;
  height: 100vh;
  position: relative;
}
.ReleaseDiv >.model {
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, .7);
  position: absolute;
  top: 0;
  left: 0;
  z-index: 100;
}
.ReleaseDiv >.model >.success {
  width: calc(334 / 1206 * 100vh);
  height: calc(119 / 1206 * 100vh);
  margin: calc(50vh - 119 / 1206 * 100vh / 2) auto 0;
  font-size: 0;
  background: url('../../static/img/success.png') center / auto 100% no-repeat;
}
.ReleaseDiv >.source {
  width: calc(485 / 1206 * 100vh);
  height: calc(491 / 1206 * 100vh);
  /* background: #f0f; */
  position: absolute;
  top: calc(224 / 1206 * 100vh);
  left: 50%;
  z-index: 10;
  margin-left: calc(-234 / 1206 * 100vh);
  /* background: url('../../static/img/Release-btn.png') center / auto 100% no-repeat; */
}
.ReleaseDiv >.source >img {
  width: 100%;
  height: 100%;
}
.ReleaseDiv >.source >video {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.ReleaseDiv >.start {
  width: 100%;
  height: 100%;
  margin: 0 auto;
  position: relative;
  z-index: 20;
  background: url('../../static/img/Release-bg.png') center / auto 100% no-repeat;
}
.ReleaseDiv >.start >div {
  /* width: 3.98rem; */
  /* height: 1.75rem; */
  width: calc(398 / 1206 * 100vh);
  height: calc(175 / 1206 * 100vh);
  font-size: 0;
  position: absolute;
  left: 50%;
  margin-left: calc(-398 / 1206 * 100vh / 2);
  cursor: pointer;
}
.ReleaseDiv >.start >.back {
  width: calc(153 / 1206 * 100vh);
  height: calc(65 / 1206 * 100vh);
  font-size: 0;
  position: absolute;
  z-index: 30;
  top: calc(42 / 1206 * 100vh);
  left: 50%;
  margin-left: calc(-700 / 1206 * 100vh / 2);
}
.ReleaseDiv >.start >.startV {
  bottom: calc(90 / 1206 * 100vh);
  background: url('../../static/img/Release-btn.png') center / auto 100% no-repeat;
}
@media screen and (max-device-aspect-ratio: 750 / 1206) {
  .ReleaseDiv {
    height: calc(100vw * 1206 / 750);
    /* margin-top: calc(50vh - (100vw * 1206 / 750) / 2); */
  }
  .ReleaseDiv >.model >.success {
    width: calc(334 / 750 * 100vw);
    height: calc(119 / 750 * 100vw);
    margin: calc(750 / 1206 * 100vh / 2) auto 0;
  }
  .ReleaseDiv >.source {
    width: calc(485 / 750 * 100vw);
    height: calc(491 / 750 * 100vw);
    position: absolute;
    top: calc(224 / 750 * 100vw);
    left: calc(141 / 750 * 100vw);
    z-index: 10;
    margin-left: 0;
  }
  .ReleaseDiv >.start >div {
    width: 3.98rem;
    height: 1.75rem;
    margin-left: calc(-3.98rem / 2);
  }
  .ReleaseDiv >.start >.back {
    width: calc(153 / 750 * 100vw);
    height: calc(65 / 750 * 100vw);
    top: calc(42 / 750 * 100vw);
    left: calc(28 / 750 * 100vw);
    margin-left: 0;
  }
  .ReleaseDiv >.start >.startV {
    bottom: .9rem;
  }
}
</style>



